/** @format */

import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Form, Formik, Field, ErrorMessage } from 'formik';
import { register } from 'redux/auth/operations';
import { RegisterSchema } from 'helpers/ModalSchemas';
import { useAuth } from 'hooks';
import Loader from 'components/Loader';
import Icon from 'components/Icon';
import styles from './regform.module.css';
import { Link } from 'react-router-dom';

const RegisterForm = () => {
	const dispatch = useDispatch();
	const [showPassword, setShowPassword] = useState(false);
	const { isLoading } = useAuth();

	const togglePasswordVisibility = () => {
		setShowPassword(!showPassword);
	};

	const handleSubmit = (values, actions) => {
		dispatch(register(values));
		actions.resetForm({ name: '', email: '', password: '' });
	};

	return isLoading ? (
		<Loader />
	) : (
		<Formik
			initialValues={{ name: '', email: '', password: '' }}
			onSubmit={(values, actions) => {
				handleSubmit(values, actions);
			}}
			validationSchema={RegisterSchema}
		>
			{({ isSubmitting }) => (
				<Form autoComplete='off'>
					<Icon name={'logo'} className={styles.icon_logo} />
					<h1 className={styles.main_text}>
						Expand your mind, reading{' '}
						<span className={styles.main_text_ext}>a book</span>
					</h1>

					<div>
						<label>
							<Field name='name' type='text' placeholder='Enter your name' />
							<ErrorMessage name='name' component='span' />
						</label>
						<label>
							<Field name='email' type='email' placeholder='Enter your email' />
							<ErrorMessage name='email' component='span' />
						</label>
						<label>
							<div>
								<Field
									name='password'
									type={showPassword ? 'text' : 'password'}
									placeholder='Create a password'
								/>
								<button
									className={styles.button_hide_password}
									type='button'
									onClick={togglePasswordVisibility}
								>
									<Icon
										name={'hide-show'}
										className={styles.icon_button_hide_password}
									/>
								</button>
							</div>

							<ErrorMessage name='password' component='span' />
						</label>
					</div>

					<button type='submit' disabled={isSubmitting}>
						<span>Register Now</span>
					</button>
					<Link to={'/login'}>Already have an account?</Link>
				</Form>
			)}
		</Formik>
	);
};

export default RegisterForm;
